.homepage-container .hero-text{text-align:center;color:#fff;margin-block-end:40px;margin-block-start:40px}.homepage-container .hero-text .main-heading{font-family:audioWide;color:#187bcf;font-size:2.5rem;text-transform:uppercase}.homepage-container .hero-text>*:not(:last-child){margin-block-end:20px}.homepage-container .hero-btn{font-family:inherit;font-size:1.1rem;color:#fff;position:relative;padding:10px 20px;border:1px #fff solid;transition:padding-inline-end,.5s}.homepage-container .hero-btn:hover{padding-inline-end:40px}.homepage-container .hero-btn:hover::after{content:"  →";position:absolute;right:12px;bottom:2px;font-size:1.7rem}