@import "../../assets/styles/setup/variables.scss";
@import "../../assets/styles/basics/helpers.scss";
@import "../../assets/styles/setup/mixins.scss";
.nav-bar {
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.849);
  position: fixed;
  width: 100%;
  .nav-logo {
    margin-inline-start: 20px;
    img {
      cursor: pointer;
      width: 115px;
    }
  }
  .nav-link {
    justify-content: center;
    width: inherit;
    cursor: pointer;
    min-height: 60px;
    color: #71beff;
    transition: color, 0.2s;
    @include for-normal-layout {
      margin-inline-end: 20px;
      img {
        width: 30px;
      }
    }

    &:hover {
      color: $clr6;
      box-shadow: inset 0 -3px 0 #71beff;
    }
  }

  .active {
    color: $clr6;
    box-shadow: inset 0 -3px 0 #71beff;
  }
}

.nav-bar-hamburger {
  font-size: 30px;
  display: flex;
  @include for-normal-layout {
    display: none;
  }
}

.nav-bar-menu {
  display: none;
  background-color: #1f2335c7;
  width: 150px;
  align-items: center;
  z-index: 5000;

  & img {
    width: 40px;
  }

  #menu:checked ~ & {
    right: 0;
    top: 59px;
    position: fixed;
    display: flex;
    flex-direction: column;
  }

  @include for-normal-layout {
    width: auto;
    background-color: inherit;
    display: flex;
    flex-direction: row;
    position: relative;

    #menu:checked ~ & {
      top: unset;
      right: unset;
      position: relative;
      display: flex;
      flex-direction: row;
    }
  }
}
